import axios from '@axios'

export default {
  namespaced: true,
  state: {
    actividad_id: 0,
    curso_taller_id: '',
    tallerGlobal_id: '',
    estudiantes: [], // participante_id, DNI, PARTICIPANTE, EMAIL, PUEBLO_INDIGENA
    evidencias: [], // id, nombre, archivo
    actividadesTallerDeportivo: [], // id,  nombre, fecha, descripcion
  },
  getters: {},
  mutations: {
    listarEstudiantes(state, data) {
      state.estudiantes = data
    },
    listarEvidencias(state, data) {
      state.evidencias = data
    },
    listarActividades(state, data) {
      state.actividadesTallerDeportivo = data
    },
  },
  actions: {
    fetchActividades(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/actividad`)
          .then(response => { ctx.commit('listarActividades', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    getActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/actividad/${id}`)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    addActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/actividad`, data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateActividad(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .put(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/actividad/${ctx.state.actividad_id}`, data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteActividad(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/actividad/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    searchParticipante(ctx, dni) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/buscar-persona/${dni}`, { withCredentials: false })
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },

    /* estudiantes */
    // eslint-disable-next-line no-unused-vars
    fetchEstudiantes(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller/${ctx.state.curso_taller_id}/matricula`)
          .then(response => { ctx.commit('listarEstudiantes', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    fetchAsistenciaEstudiantes(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller/actividad/${ctx.state.actividad_id}/participante`)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    updateAsistenciaEstudiantes(ctx, data) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/taller/actividad/${ctx.state.actividad_id}/participante`, data)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },

    /* evidencias */
    fetchEvidencias(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/servicio-deportivo/taller/actividad/${id}/evidencia`)
          .then(response => { ctx.commit('listarEvidencias', response.data.data); resolve(response) })
          .catch(error => reject(error))
      })
    },
    addEvidencia(ctx, data) {
      const config = { headers: { 'Content-Type': 'multipart/formdata' } }
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-deportivo/taller/actividad/${data.get('id')}/evidencia`, data, config)
          .then(response => { resolve(response) })
          .catch(error => reject(error))
      })
    },
    deleteEvidencia(ctx, id) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/servicio-deportivo/taller/actividad/evidencia/${id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchCursoTaller(ctx) {
      return new Promise((resolve, reject) => {
        axios
          .get(`servicio-deportivo/taller-global/${ctx.state.tallerGlobal_id}/taller/${ctx.state.curso_taller_id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
