<template>
  <div>
    <!-- Breadcrumb -->
    <div class="d-flex justify-content-start align-items-center">
      <b-button
        class="mr-1"
        variant="outline-primary"
        style="padding: 5px 10px"
        :to="{ name: 'ver-actividades-taller-deporte', params: { id: taller_id, tallerGlobal_id: tallerGlobal_id } }"
      >
        <feather-icon
          icon="ChevronsLeftIcon"
        />
        volver
      </b-button>
      <span class="border-right pr-1 mr-1"><h3>Deportivo</h3></span>
      <feather-icon
        icon="HomeIcon"
        class="text-primary"
        size="16"
      />
      <feather-icon
        icon="ChevronRightIcon"
      />
      <b-breadcrumb
        :items="itemsBreadcrumb"
      />
    </div>
    <br>
    <!-- Breadcrumb End -->
    <b-card class="mb-1">
      <b-row>
        <b-col
          lg="7"
          md="7"
          sm="12"
        >
          <h5 class="">
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">TALLER DEPORTIVO : </strong>{{ nombre_taller }}
            ( <b-badge
              :variant="(estado_taller == 1) ? 'light-success' : 'light-danger'"
              class="px-2"
            >
              {{ (estado_taller == 1) ? 'ABIERTO' : 'CERRADO' }}
            </b-badge> )
          </h5>
          <h5>
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">SEMESTRE : </strong>{{ nombre_taller }}
          </h5>

        </b-col>
        <b-col
          lg="5"
          md="5"
          sm="12"
        >
          <h5 class="">
            <feather-icon
              icon="PlayIcon"
            />
            <strong class="text-primary">DOCENTE : </strong>{{ nombre_taller }}
          </h5>

        </b-col>
      </b-row>
    </b-card>
    <form-wizard
      next-button-text="Siguiente"
      back-button-text="Anterior"
      color="#06634c"
      :title="null"
      :subtitle="null"
      layout="vertical"
      finish-button-text="Terminar Proceso"
      class="wizard-vertical mb-3"
      @on-complete="formSubmitted"
      @on-change="onchange"
    >
      <tab-content
        title="Crear Actividad"
        :before-change="validationFormInfo"
      >
        <validation-observer
          ref="infoRules"
          tag="form"
        >
          <b-row>
            <b-col
              cols="12"
              class="mb-2"
            >
              <h5 class="mb-0">
                Registro de nueva actividad
              </h5>
              <small class="text-muted">
                Detalle la actividad a crear
              </small>
            </b-col>
            <b-col md="8">
              <b-form-group
                label="Actividad"
                label-for="v-actividad"
              >
                <validation-provider
                  #default="{ errors }"
                  name="actividad"
                  rules="required"
                >
                  <b-form-input
                    id="v-actividad"
                    v-model="actividad.nombre"
                    :state="errors.length > 0 ? false:null"
                    placeholder=""
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group

                label="Fecha"
                label-for="v-fecha"
              >
                <validation-provider
                  #default="{ errors }"
                  name="fecha"
                  rules="required"
                >
                  <b-form-input
                    id="v-fecha"
                    v-model="actividad.fecha"
                    type="date"
                    :state="errors.length > 0 ? false:null"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group

                label="Descripción (opcional)"
                label-for="v-descripcion"
              >

                <b-form-textarea
                  id="v-descripcion"
                  v-model="actividad.descripcion"
                  placeholder="Descripción de la actividad"
                />

              </b-form-group>
            </b-col>

          </b-row>
        </validation-observer>
      </tab-content>
      <tab-content title="estudiantes">
        <b-row>
          <b-col
            cols="6"
            class="mb-2"
          >
            <h5 class="mb-0">
              Estudiantes
            </h5>
            <small class="text-muted">Estudiantes</small>
          </b-col>
          <b-col
            cols="6"
            class="my-1 float-right"
          >
            <b-form-group
              class="mb-0"
            >
              <b-input-group size="sm">
                <b-form-input
                  id="filter-input"
                  v-model="filter"
                  type="search"
                  placeholder="Buscar por DNI o Apellidos y nombres"
                />

                <b-input-group-append>
                  <b-button
                    :disabled="!filter"
                    size=""
                    @click="filter = ''"
                  >
                    Limpiar
                  </b-button>
                </b-input-group-append>
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col md="12">
            <b-form-checkbox-group
              id="checkbox-group-2"
              v-model="selected"
              checked
              name="flavour-2"
            >
              <b-table
                :items="arrayEstudiantes"
                :fields="fieldEstudiantes"
                :filter="filter"
                sticky-header="380px"
                small
                head-variant="light"
              >
                <template #cell(actions)="row">
                  <b-form-checkbox
                    switch
                    :value="row.item.alumno_id"
                    name="check"
                  />
                </template>
                <template #cell(numero_documento)="row">
                  {{ row.item.persona_alumno.numero_documento }}
                </template>
                <template #cell(apellido_paterno)="row">
                  {{ row.item.persona_alumno.apellido_paterno }}
                </template>
                <template #cell(apellido_materno)="row">
                  {{ row.item.persona_alumno.apellido_materno }}
                </template>
                <template #cell(nombres)="row">
                  {{ row.item.persona_alumno.nombres }}
                </template>
                <template #cell(pueblo_indigena)="row">
                  {{ row.item.persona_alumno.pueblo_indigena.nombre }}
                </template>

              </b-table>
            </b-form-checkbox-group>
          </b-col>
        </b-row>
      </tab-content>
      <!-- address -->
      <tab-content
        title="Evidencias"
        :before-change="validationFormInfo"
      >
        <b-row>
          <b-col
            cols="12"
            class="mb-2"
          >
            <h5 class="mb-0">
              Evidencias
            </h5>
            <small class="text-muted">Registro de evidencias</small>
          </b-col>
          <b-col md="5">
            <b-form-group
              label="Nombre Evidencia"
              label-for="nueva_evidencia"
            >
              <b-form-input
                id="nueva_evidencia"
                v-model="evidenciaNueva.nombre"
                size="sm"
              />
            </b-form-group>
          </b-col>
          <b-col md="4">
            <b-form-group
              label="Archivo"
              label-for="archivo"
            >
              <b-form-file
                v-model="evidenciaNueva.archivo"
                size="sm"
              />
            </b-form-group>
          </b-col>
          <b-col md="3">
            <b-button
              size="sm"
              variant="success"
              class="mt-2"
              @click="adicionarEvidencia()"
            >
              <feather-icon icon="ArrowUpCircleIcon" />
              Add archivo
            </b-button>
          </b-col>
          <b-col md="12">
            <b-table
              :items="evidencias"
              :fields="fieldEvidencias"
              sticky-header="300px"
              small
              head-variant="light"
              class="mb-3"
            >
              <template #cell(actions)="row">
                <b-button
                  size="sm"
                  class="mr-1"
                  variant="outline-danger"
                  @click="eliminarEvidencia(row.item.id)"
                >
                  eliminar
                  <feather-icon icon="Trash2Icon" />
                </b-button>
              </template>
              <template #cell(archivo)="row">
                <a
                  :href="row.item.archivo"
                  class="btn btn-sm btn-outline-success"
                  target="_blank"
                >ver evidencia
                  <feather-icon icon="EyeIcon" />
                </a>
              </template>
            </b-table>
            <br>
            <br>
          </b-col>
        </b-row>
      </tab-content>
    </form-wizard>

  </div>
</template>

<script>
import { FormWizard, TabContent } from 'vue-form-wizard'
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import { required } from '@validations'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import {
  BRow,
  BCol,
  BFormGroup,
  BFormInput,
  BBreadcrumb,
  BFormTextarea,
  BButton,
  BTable,
  BInputGroupAppend,
  BInputGroup,
  BFormFile,
  BFormCheckbox,
  BFormCheckboxGroup,
  BCard,
  BBadge,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import store from '@/store'
import { onUnmounted } from '@vue/composition-api'
import { mapState } from 'vuex'

import actDeportivasStoreModule from './actDeportivasTallerStoreModule'

export default {
  components: {
    ValidationProvider,
    ValidationObserver,
    BBreadcrumb,
    FormWizard,
    TabContent,
    BRow,
    BCol,
    BTable,
    BInputGroupAppend,
    BInputGroup,
    BButton,
    BFormGroup,
    BFormInput,
    BFormTextarea,
    BFormFile,
    BFormCheckbox,
    BFormCheckboxGroup,
    BCard,
    BBadge,
  },
  data() {
    return {
      selected: [],
      nombre_taller: '',
      estado_taller: '',
      taller_id: this.$attrs.taller_id,
      actividad_id: this.$attrs.id,
      evidenciaNueva: {
        nombre: '',
        archivo: null,
      },
      arrayEstudiantes: [],
      arrayAsistencia: {
        actividad_id: '',
        asistencia: [],
      },
      fieldEvidencias: [
        {
          key: 'nombre', label: 'Nombre', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'archivo', label: 'Evidencia', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'actions', label: 'Eliminar', sortable: true, sortDirection: 'asc',
        },
      ],
      fieldEstudiantes: [
        {
          key: 'numero_documento', label: 'DNI', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'apellido_paterno', label: 'Ap.Paterno', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'apellido_materno', label: 'Ap.Materno', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'nombres', label: 'Nombres', sortable: true, sortDirection: 'asc',
        },
        {
          key: 'pueblo_indigena', label: 'Pueblo Indigena', sortable: true, sortDirection: 'asc',
        },
        { key: 'actions', label: 'Asistencia' },
      ],
      filter: null,
      // estudiantes: [],
      required,
      actividad: {
        id: '',
        nombre: '',
        fecha: '',
        descripcion: '',
      },

      itemsBreadcrumb: [
        {
          text: 'Talleres Globales',
          to: { name: 'servicio-deportivo' },
        },
        {
          text: 'Talleres',
          to: { name: 'curso-taller-deportivo', params: { id: store.state.actividades_taller.tallerGlobal_id } },
        },
        {
          text: 'Actividades',
          to: { name: 'ver-actividades-taller-deporte', params: { id: this.$router.currentRoute.params.taller_id, tallerGlobal_id: store.state.actividades_taller.tallerGlobal_id } },
        },
        {
          text: 'Actividad',
          active: true,
        },
      ],
    }
  },
  computed: {
    ...mapState('actividades_taller', ['tallerGlobal_id', 'estudiantes', 'evidencias', 'tabla', 'talleresSegunDeporte', 'tituloModal', 'tituloBoton', 'guardarEditar', 'cursoTallerNuevo']),
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => ({ text: f.label, value: f.key }))
    },
  },

  mounted() {
    store.state.actividades_taller.actividad_id = this.actividad_id
    store.state.actividades_taller.taller_id = this.taller_id
    store.state.actividades_taller.curso_taller_id = this.taller_id

    store.dispatch('actividades_taller/fetchEvidencias', this.actividad_id)

    store.dispatch('actividades_taller/getActividad', this.actividad_id).then(response => {
      this.actividad.id = this.actividad_id
      this.actividad.nombre = response.data.data.nombre
      this.actividad.fecha = response.data.data.fecha
      this.actividad.descripcion = response.data.data.descripcion
    }).catch(error => {
      console.log(error)
    })

    // Recuperamos taller_global_id
    this.$http.get(`/servicio-deportivo/taller/${this.$router.currentRoute.params.taller_id}`).then(res => {
      store.state.actividades_taller.tallerGlobal_id = res.data.taller_global_id
      this.$http.get(`servicio-deportivo/taller-global/${res.data.taller_global_id}/taller/${this.taller_id}`).then(response => {
        this.nombre_taller = response.data.data.nombre
        this.estado_taller = response.data.data.estado
      })
    })
  },
  methods: {

    adicionarEvidencia() {
      if (this.evidenciaNueva.nombre === '' || this.evidenciaNueva.archivo === null) {
        this.toast('Campo nombre o archivo vacio ', 'warning', 'Advertencia')
        return
      }
      const formData = new FormData()
      formData.append('id', this.actividad_id)
      formData.append('nombre', this.evidenciaNueva.nombre)
      formData.append('archivo', this.evidenciaNueva.archivo)
      store.dispatch('actividades_taller/addEvidencia', formData).then(() => {
        this.toast('¡Evidencia registrado!', 'success', 'Éxito')
        store.dispatch('actividades_taller/fetchEvidencias', this.actividad_id)
        this.evidenciaNueva = []
      }).catch(error => {
        console.log(error)
      })
    },
    eliminarEvidencia(id) {
      store.dispatch('actividades_taller/deleteEvidencia', id).then(res => {
        this.swalSuccess(`Evidencia ${res.data.data.nombre} eliminada`)
        store.dispatch('actividades_taller/fetchEvidencias', this.actividad_id)
      })
    },
    validationFormInfo() {
      return new Promise((resolve, reject) => {
        this.$refs.infoRules.validate().then(success => {
          if (success) {
            resolve(true)
          } else {
            reject()
          }
        })
      })
    },
    toast(text, tipo, titulo) {
      this.$toast({
        component: ToastificationContent,
        props: {
          title: titulo,
          icon: 'BellIcon',
          text,
          variant: tipo,
        },
      })
    },
    formSubmitted() {
      if (this.actividad_id === 0) {
        this.toast('Actividad guardada con exito!!', 'success', 'Éxito')
      } else {
        this.toast('Actividad actualizada con exito!!', 'success', 'Éxito')
      }
      this.$router.push({ name: 'ver-actividades-taller-deporte', params: { id: this.taller_id, tallerGlobal_id: store.state.actividades_taller.tallerGlobal_id } })
    },
    swalSuccess(texto) {
      this.$swal({
        position: 'top-end',
        icon: 'success',
        title: texto,
        showConfirmButton: false,
        timer: 1500,
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },

    onchange(prevIndex) {
      switch (prevIndex) {
        case 0: this.guardarActualizar()
          // this.textoBotonNext = ''
          break
        case 1: this.actualizarAsistencia()
          break
        case 2:
          break
        default:
          break
      }
    },
    guardarActualizar() {
      if (parseInt(this.actividad_id, 10) === 0) { // true : Registra nueva actividad
        store.dispatch('actividades_taller/addActividad', this.actividad).then(response => {
          this.actividad_id = response.data.data.id
          store.state.actividades_taller.actividad_id = response.data.data.id
          // VAMOS A PROBAR ESTE SCRIPT
          this.recuperarAsistencia()
        }).catch(error => {
          console.log(error)
        })
      } else { // false: Actualizamos actividad
        store.dispatch('actividades_taller/updateActividad', this.actividad).then(response => {
          this.actividad_id = response.data.data.id
          store.state.actividades_taller.actividad_id = response.data.data.id
          this.recuperarAsistencia()
        }).catch(error => {
          console.log(error)
        })
      }
    },
    recuperarAsistencia() {
      store.dispatch('actividades_taller/fetchEstudiantes', this.actividad_id).then(res2 => {
        this.arrayEstudiantes = res2.data.data
        store.dispatch('actividades_taller/fetchAsistenciaEstudiantes').then(res3 => {
          this.arrayAsistencia = res3.data.data
          if (this.arrayAsistencia !== undefined) {
            this.arrayAsistencia.forEach(value => {
              this.arrayEstudiantes.forEach(value2 => {
                if (value.participante_id === value2.alumno_id) {
                  this.selected.push(value.participante_id)
                }
              })
            })
          }
        })
      })
    },
    actualizarAsistencia() {
      const formdata = new FormData()
      if (this.selected.length !== 0) {
        this.selected.forEach((value, index) => {
          formdata.append(`participante_id[${index}]`, value)
        })
      } else {
        formdata.append('participante_id[]', '')
      }
      store.dispatch('actividades_taller/updateAsistenciaEstudiantes', formdata)
    },
  },
  setup() {
    const ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME = 'actividades_taller'

    // Register module
    if (!store.hasModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)) store.registerModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME, actDeportivasStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)) store.unregisterModule(ACTIVIDADES_TALLER_APP_STORE_MODULE_NAME)
    })

    return {

    }
  },
}
</script>
<style lang="scss">
  @import '@core/scss/vue/libs/vue-wizard.scss';
  @import '@core/scss/vue/libs/vue-select.scss';
</style>
<style scoped>
 .wizard-icon-container{
   background-color: red;
 }
</style>
